.wrapper {
  .genSkills {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 35px;

    .skillsText {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 50vw;
      gap: 10px;

      .small {
        color: #aaa;
        font-size: 1rem;
      }

      .title {
        span {
          font-size: 3rem;
          font-weight: 900;
          &:hover {
            transition: all 0.3s ease-in-out;
            color: #ff9100;
            cursor: pointer;
          }
        }
      }

      .description {
        font-size: 1rem;
      }

      .link {
        text-decoration: none;
        color: #ff9100;

        &:hover {
          opacity: 0.5;
        }
      }
    }

    .langs {
      display: flex;
      flex-wrap: wrap;
      width: 65vw;
      gap: 48px;
      justify-content: center;

      svg {
        display: flex;
        width: 4.5rem;
        height: 4.5rem;
        &:hover {
          transform: translateY(-5px);
          cursor: pointer;
          filter: invert(58%) sepia(39%) saturate(3298%) hue-rotate(0deg) brightness(150%)
            contrast(105%);
        }
      }
    }
  }
}
